"use client";

import React, { useEffect, useMemo, useRef, useState } from "react";
import LiveAnalyzer from "./live-analyzer";
import LiveChat from "./live-chat";

type PersistedUI = {
  symbol?: string;
  lookback?: number;
  timeframesText?: string;
  accountId?: string;
};

/**
 * Keep Chat aligned with Analyzer without lifting state.
 * Support multiple versions so UI updates don't silently break sync.
 */
const LS_KEYS = ["liveAnalyzerUI:v5", "liveAnalyzerUI:v4"];

function safeJsonParse<T>(raw: string | null): T | null {
  if (!raw) return null;
  try {
    return JSON.parse(raw) as T;
  } catch {
    return null;
  }
}

function parseTimeframes(text: string): string[] {
  const parts = text
    .split(/[,\s]+/g)
    .map((s) => s.trim())
    .filter(Boolean);

  const seen = new Set<string>();
  const out: string[] = [];
  for (const p of parts) {
    if (!seen.has(p)) {
      seen.add(p);
      out.push(p);
    }
  }
  return out;
}

function readPersistedUI(): PersistedUI | null {
  if (typeof window === "undefined") return null;
  for (const k of LS_KEYS) {
    const ui = safeJsonParse<PersistedUI>(window.localStorage.getItem(k));
    if (ui) return ui;
  }
  return null;
}

export default function LivePage() {
  // Chat defaults (safe)
  const [chatSymbol, setChatSymbol] = useState<string>("BTCUSDT");
  const [chatTimeframes, setChatTimeframes] = useState<string[]>(["1h", "4h", "12h", "1d"]);
  const [chatLookback, setChatLookback] = useState<number>(300);
  const [chatAccountId, setChatAccountId] = useState<string>("");

  const lastAppliedRef = useRef<string>("");

  useEffect(() => {
    let mounted = true;
    let pollId: number | null = null;

    const apply = (ui: PersistedUI) => {
      const symbol = (ui.symbol || "").trim();
      const lookback = Number(ui.lookback);
      const tfsText = (ui.timeframesText || "").trim();

      // Build a stable signature so we don't rerender on identical storage reads
      const sig = JSON.stringify({
        symbol: symbol || undefined,
        lookback: Number.isFinite(lookback) && lookback > 0 ? lookback : undefined,
        timeframesText: tfsText || undefined,
      });

      if (sig === lastAppliedRef.current) return;
      lastAppliedRef.current = sig;

      if (!mounted) return;

      if (symbol) setChatSymbol(symbol);
      if (Number.isFinite(lookback) && lookback > 0) setChatLookback(lookback);
      if (tfsText) {
        const tfs = parseTimeframes(tfsText);
        if (tfs.length) setChatTimeframes(tfs);
      }
      if (ui.accountId) setChatAccountId(ui.accountId);
    };

    const syncFromStorage = () => {
      const ui = readPersistedUI();
      if (!ui) return;
      apply(ui);
    };

    // initial
    syncFromStorage();

    // cross-tab changes
    const onStorage = (e: StorageEvent) => {
      if (!e.key) return;
      if (LS_KEYS.includes(e.key)) syncFromStorage();
    };
    window.addEventListener("storage", onStorage);

    // same-tab updates: storage event does not fire in same tab, so poll
    const startPoll = () => {
      if (pollId != null) return;
      pollId = window.setInterval(syncFromStorage, 1200);
    };
    const stopPoll = () => {
      if (pollId == null) return;
      window.clearInterval(pollId);
      pollId = null;
    };

    // pause polling when tab is hidden (reduces useless load)
    const onVis = () => {
      if (document.visibilityState === "hidden") stopPoll();
      else startPoll();
    };
    document.addEventListener("visibilitychange", onVis);

    startPoll();

    return () => {
      mounted = false;
      window.removeEventListener("storage", onStorage);
      document.removeEventListener("visibilitychange", onVis);
      stopPoll();
    };
  }, []);

  const chatProps = useMemo(
    () => ({ symbol: chatSymbol, timeframes: chatTimeframes, lookback: chatLookback, accountId: chatAccountId }),
    [chatSymbol, chatTimeframes, chatLookback, chatAccountId]
  );

  return (
    <div className="page">
      <header className="header">
        <h1 className="title">Live Analyzer</h1>
        <p className="subtitle">
          Analyzer uses <code>/api/analyze</code>. Chat uses <code>/api/chat</code>. Chat auto-syncs from Analyzer UI
          state stored in localStorage.
        </p>

        <div className="chips">
          <span className="chip">
            Chat symbol: <b>{chatSymbol}</b>
          </span>
          <span className="chip">
            TFs: <b>{chatTimeframes.join(", ")}</b>
          </span>
          <span className="chip">
            Lookback: <b>{chatLookback}</b>
          </span>
        </div>
      </header>

      <div className="grid">
        <LiveAnalyzer />
        <LiveChat {...chatProps} />
      </div>

      <style>{`
        .page {
          padding: 16px;
          max-width: 1240px;
          margin: 0 auto;
        }
        .header {
          display: grid;
          gap: 6px;
          margin-bottom: 14px;
        }
        .title {
          font-size: 22px;
          font-weight: 900;
          margin: 0;
        }
        .subtitle {
          opacity: 0.75;
          margin: 0;
          line-height: 1.35;
        }
        .chips {
          margin-top: 8px;
          display: flex;
          flex-wrap: wrap;
          gap: 8px;
          align-items: center;
          opacity: 0.9;
          font-size: 12px;
        }
        .chip {
          padding: 6px 10px;
          border-radius: 999px;
          border: 1px solid rgba(255, 255, 255, 0.14);
        }
        .grid {
          display: grid;
          grid-template-columns: minmax(0, 1fr) 420px;
          gap: 16px;
          align-items: start;
        }
        code {
          font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New",
            monospace;
        }
        @media (max-width: 1100px) {
          .grid {
            grid-template-columns: 1fr;
          }
        }
      `}</style>
    </div>
  );
}
