"use client";

import * as React from "react";

type ChatIntent = "explain" | "trade";
type TradeMode = "HTF" | "LTF_ONLY";

type ChatResponse = {
  answer_markdown?: string;
  answer?: string;
  forwarded?: { mode?: string };
  plan?: any;
  [k: string]: any;
};

function clamp(n: number, lo: number, hi: number) {
  if (!Number.isFinite(n)) return lo;
  return Math.max(lo, Math.min(hi, n));
}

function n2(x: any, dp = 2) {
  const num = typeof x === "string" ? Number(x) : (x as number);
  if (!Number.isFinite(num)) return String(x ?? "?");
  return num.toFixed(dp);
}

function safeJson(x: any) {
  try {
    return JSON.stringify(x, null, 2);
  } catch {
    return String(x);
  }
}

export default function LiveChat(props: { symbol: string; timeframes: string[]; lookback: number; accountId?: string }) {
  const [question, setQuestion] = React.useState("");
  const [intent, setIntent] = React.useState<ChatIntent>("explain");

  // trade controls
  const [tradeMode, setTradeMode] = React.useState<TradeMode>("HTF");
  const [aggressive, setAggressive] = React.useState(false);
  const [maxRiskPct, setMaxRiskPct] = React.useState(3); // percent of equity
  const [minRR, setMinRR] = React.useState(2.5);

  // outputs
  const [answer, setAnswer] = React.useState<string | null>(null);
  const [tradeResult, setTradeResult] = React.useState<ChatResponse | null>(null);

  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState<string | null>(null);

  const [dataWarning, setDataWarning] = React.useState<string | null>(null);

  const formatTradeBrief = React.useCallback(
    (resp: ChatResponse): string => {
      const analyze = (resp ?? {}) as any;
      if (!analyze) return "No trade response returned.";

      const snap = analyze?.snapshot ?? analyze?.plan?.snapshot;
      const verdict = analyze?.verdict ?? analyze?.plan?.verdict;
      const planObj = analyze?.plan ?? analyze;

      const forwardedMode = resp?.forwarded?.mode ?? analyze?.mode ?? tradeMode;

      const lines: string[] = [];
      lines.push(`Symbol: ${snap?.symbol ?? props.symbol ?? "?"}`);
      lines.push(`Mode: ${forwardedMode}`);
      if (snap?.timeframes?.length) {
        const tfs = (snap.timeframes as any[]).map((t) => (typeof t === "string" ? t : t?.tf)).filter(Boolean);
        if (tfs.length) lines.push(`TFs: ${tfs.join(", ")}`);
      }
      lines.push("");

      const gate = snap?.gate ?? analyze?.gate;
      if (gate) {
        lines.push(
          `HTF Gate: ${gate.blocked ? "BLOCKED" : "PASS"}${
            Array.isArray(gate.higherTimeframesUsed) && gate.higherTimeframesUsed.length
              ? ` (${gate.higherTimeframesUsed.join(", ")})`
              : ""
          }`
        );
        if (gate.reasons?.length) lines.push(`Gate reasons: ${gate.reasons.join(" | ")}`);
        lines.push("");
      }

      lines.push(`Action: ${planObj?.action ?? "?"}`);
      if (planObj?.leverage != null) lines.push(`Leverage: ${planObj.leverage}x`);

      if (planObj?.entry) {
        const entryType = planObj.entry.type ?? "market";
        const entryPrice = planObj.entry.price ?? "market";
        lines.push(`Entry: ${entryType} @ ${entryPrice} (trigger ${planObj.entry.trigger ?? "n/a"})`);
      }
      if (planObj?.stop) {
        const stopLine = `Stop: ${planObj.stop.price ?? "?"}${planObj.stop.rationale ? ` — ${planObj.stop.rationale}` : ""}`;
        lines.push(stopLine);
      }

      if (Array.isArray(planObj?.targets) && planObj.targets.length) {
        lines.push("Targets:");
        for (const t of planObj.targets) lines.push(`- ${t.price} (${t.sizePct ?? "?"}%)`);
      }

      if (planObj?.riskPct != null) lines.push(`Risk: ${planObj.riskPct}%`);
      if (planObj?.notes) lines.push(`Notes: ${planObj.notes}`);

      // Execution sizing (new in route)
      const exec = analyze?.execution ?? planObj?.execution;
      if (exec) {
        lines.push("");
        lines.push("Execution:");
        if (exec.qty != null) lines.push(`- Qty: ${n2(exec.qty, 6)}`);
        if (exec.notionalUsd != null) lines.push(`- Notional: $${n2(exec.notionalUsd, 2)}`);
        if (exec.effectiveLeverage != null) lines.push(`- Eff Lev: ${n2(exec.effectiveLeverage, 2)}x`);
        if (exec.riskUsdPlanned != null) lines.push(`- Risk planned: $${n2(exec.riskUsdPlanned, 2)}`);
        if (exec.riskUsdActual != null) lines.push(`- Risk actual:  $${n2(exec.riskUsdActual, 2)}`);

        if (Array.isArray(exec.targets) && exec.targets.length) {
          lines.push("- Targets (PnL / RR):");
          for (const t of exec.targets) {
            const rr = t.rr != null ? n2(t.rr, 2) : "?";
            const pnl = t.pnlUsd != null ? `$${n2(t.pnlUsd, 2)}` : "?";
            lines.push(`  • ${t.price} -> ${pnl} | RR ${rr}`);
          }
        }
      }

      if (verdict) {
        lines.push("");
        lines.push(
          `Validator: ${verdict.ok ? "OK" : "FAIL"}${
            verdict.rr != null ? ` | RR=${Number(verdict.rr).toFixed(2)}` : ""
          }`
        );
        if (verdict.reasons?.length) lines.push(`Reasons: ${verdict.reasons.join(" | ")}`);
      }

      return lines.join("\n");
    },
    [props.symbol, tradeMode]
  );

  const ask = React.useCallback(async () => {
    const q = question.trim();
    if (!q) return;

    setLoading(true);
    setError(null);
    setAnswer(null);
    setTradeResult(null);
    setDataWarning(null);

    try {
      const equityUsd = 100;

      // UI guardrails
      const constraints = {
        maxLeverage: 10, // hard cap to match your requirement
        maxRiskPct: clamp(Number(maxRiskPct), 0.25, 10),
        minRR: clamp(Number(minRR), 1, 10),
      };

      const body: any = {
        intent,
        symbol: props.symbol,
        timeframes: props.timeframes,
        lookback: props.lookback,
        message: q,
        question: q,
        accountId: props.accountId,

        equityUsd,
        aggressive: intent === "trade" ? aggressive : false,
        constraints,

        // Back-compat keys if your /api/chat expects them
        tradeMode: tradeMode,
        wsProbe:
          intent === "trade"
            ? { durationMs: 5000, maxMessages: 2000 }
            : { durationMs: 0, maxMessages: 0 },

        // LTF plan (only when LTF mode is selected)
        ltfPlan:
          intent === "trade" && tradeMode === "LTF_ONLY"
            ? { enabled: true, timeframes: ["4h", "1h", "15m"], minBars: 150 }
            : undefined,

        // Optional RAG filters (leave null unless you implement server-side)
        doc_names: null,
        chunk_types: null,
      };

      const res = await fetch("/api/chat", {
        method: "POST",
        headers: { "content-type": "application/json" },
        body: JSON.stringify(body),
      });

      const j: ChatResponse = await res.json();
      if (!res.ok) throw new Error((j as any)?.error || `HTTP ${res.status}`);

      // Data health: if server reports no candle rows / missing snapshot data, surface actionable guidance.
      try {
        const snap: any = (j as any)?.snapshot;
        const counts = snap?.counts ?? snap?.rowsByTimeframe ?? snap?.tfCounts;
        const totalRows = Array.isArray(counts)
          ? counts.reduce((a, c) => a + Number(c?.rows ?? c?.count ?? 0), 0)
          : counts && typeof counts === "object"
            ? Object.values(counts as Record<string, any>).reduce((a, c) => a + Number(c ?? 0), 0)
            : undefined;
        const msg = String((j as any)?.answer_markdown ?? (j as any)?.answer ?? "");
        if (totalRows === 0 || /missing recent price data|no candle records|no candle/i.test(msg)) {
          setDataWarning(
            "No recent candle data was found for the requested symbol/timeframes. Check that your streamer is running, the symbol matches exactly (e.g. BTCUSDT), and that your candles table has rows for 1h/4h/12h/1d. If rows exist but snapshots are empty, verify timeframe strings and open_time timezone / ingestion mapping."
          );
        }
      } catch {}

      if (intent === "trade") {
        setTradeResult(j);
      } else {
        const answerText = j.answer_markdown ?? j.answer ?? "(no answer)";
        setAnswer(answerText);
      }
    } catch (e: any) {
      setError(e?.message || "Unknown error");
    } finally {
      setLoading(false);
    }
  }, [aggressive, intent, maxRiskPct, minRR, props.lookback, props.symbol, props.timeframes, question, tradeMode]);

  const onKeyDown = React.useCallback(
    (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
      if ((e.ctrlKey || e.metaKey) && e.key === "Enter") {
        e.preventDefault();
        void ask();
      }
    },
    [ask]
  );

  return (
    <div style={{ border: "1px solid rgba(255,255,255,0.12)", borderRadius: 10, padding: 12 }}>
      <div style={{ display: "flex", justifyContent: "space-between", gap: 12, alignItems: "baseline" }}>
        <div style={{ fontWeight: 800 }}>Live Chat</div>
        <div style={{ opacity: 0.75, fontSize: 12 }}>
          {props.symbol} | {props.timeframes.join(", ")} | lb {props.lookback}
        </div>
      </div>

      <div style={{ display: "flex", gap: 12, marginTop: 10, flexWrap: "wrap", alignItems: "center" }}>
        <label>
          <input type="radio" checked={intent === "explain"} onChange={() => setIntent("explain")} /> Explain
        </label>
        <label>
          <input type="radio" checked={intent === "trade"} onChange={() => setIntent("trade")} /> Trade
        </label>

        {intent === "trade" && (
          <>
            <span style={{ opacity: 0.7 }}>|</span>

            <label>
              Mode{" "}
              <select
                value={tradeMode}
                onChange={(e) => setTradeMode(e.target.value as TradeMode)}
                style={{ marginLeft: 6 }}
              >
                <option value="HTF">HTF</option>
                <option value="LTF_ONLY">LTF (4h/1h/15m)</option>
              </select>
            </label>

            <label style={{ display: "flex", alignItems: "center", gap: 6 }}>
              <input type="checkbox" checked={aggressive} onChange={(e) => setAggressive(e.target.checked)} /> Aggressive
            </label>

            <label style={{ display: "flex", alignItems: "center", gap: 6 }}>
              Max risk %
              <input
                type="number"
                value={maxRiskPct}
                onChange={(e) => setMaxRiskPct(Number(e.target.value))}
                min={0.25}
                max={10}
                step={0.25}
                style={{ width: 80 }}
              />
            </label>

            <label style={{ display: "flex", alignItems: "center", gap: 6 }}>
              Min RR
              <input
                type="number"
                value={minRR}
                onChange={(e) => setMinRR(Number(e.target.value))}
                min={1}
                max={10}
                step={0.25}
                style={{ width: 80 }}
              />
            </label>

            <div style={{ opacity: 0.75, fontSize: 12 }}>Equity: $100 (paper) | Max Lev: 10x</div>
          </>
        )}
      </div>

      <textarea
        value={question}
        onChange={(e) => setQuestion(e.target.value)}
        onKeyDown={onKeyDown}
        placeholder={
          intent === "trade"
            ? "Describe the setup or ask for a call. (Ctrl/Cmd+Enter to run)"
            : "Ask about structure, regime, EMA/RSI, gate state, invalidations… (Ctrl/Cmd+Enter)"
        }
        rows={4}
        style={{ width: "100%", padding: 10, borderRadius: 8, marginTop: 10 }}
      />

      <div style={{ display: "flex", gap: 8, marginTop: 8 }}>
        <button onClick={() => void ask()} disabled={loading} style={{ padding: "8px 10px" }}>
          {loading ? "Running…" : intent === "trade" ? "Run Trade" : "Ask"}
        </button>

        <button
          onClick={() => {
            setQuestion("");
            setAnswer(null);
            setTradeResult(null);
    setDataWarning(null);
            setError(null);
          }}
          disabled={loading}
          style={{ padding: "8px 10px" }}
        >
          Clear
        </button>

        <div style={{ marginLeft: "auto", opacity: 0.7, fontSize: 12, alignSelf: "center" }}>
          Tip: Ctrl/Cmd+Enter
        </div>
      </div>

      {error && <div style={{ marginTop: 10, color: "tomato" }}>{error}</div>}

      {answer && <div style={{ marginTop: 10, whiteSpace: "pre-wrap", opacity: 0.95 }}>{answer}</div>}

      {tradeResult && (
        <>
          <div style={{ marginTop: 10, whiteSpace: "pre-wrap", opacity: 0.95 }}>
            {formatTradeBrief(tradeResult)}
          </div>

          <details style={{ marginTop: 10 }}>
            <summary style={{ cursor: "pointer", opacity: 0.85 }}>Show raw JSON</summary>
            <pre
              style={{
                marginTop: 10,
                padding: 10,
                borderRadius: 8,
                background: "rgba(0,0,0,0.2)",
                fontSize: 12,
                overflowX: "auto",
                overflowY: "auto",
                maxHeight: 360,
                whiteSpace: "pre-wrap",
                wordBreak: "break-word",
              }}
            >
              {safeJson(tradeResult)}
            </pre>
          </details>
        </>
      )}
    </div>
  );
}
